import React from 'react'

export default function LogInfo (props) {
  return (
    <table className='ServiceLog-info'>
      <tbody>
        <tr>
          {props.infobox.map((info, index) => (
            <td key={index}>
              <em>{info.key}:</em> <data>{info.value}</data>
            </td>
          ))}
        </tr>
      </tbody>
    </table>
  )
}
