import React from 'react'
import SyntaxHighlighter from 'react-syntax-highlighter'
import { magula } from 'react-syntax-highlighter/dist/esm/styles/hljs'

export default function LogRequest (props) {
  return (
    <>
      <hr />
      <section className='ServiceLog-request'>
        <header className='ServiceLog-requestHeader'>
          <p><em>Interface:</em> {props.name}</p>
          <p><em>Endpoint:</em> {props.endpoint}</p>
          <p><em>Data:</em> {props.requestTime.dateText} {props.requestTime.timeText}-{props.responseTime.timeText}</p>
        </header>
        <nav className='ServiceLog-nav'>
          <button className='ServiceLog-navButton'>
              Cabeçalho
          </button>
          <button className='ServiceLog-navButton'>
              Request
          </button>
          <button className='ServiceLog-navButton'>
              Response
          </button>
          <button className='ServiceLog-navButton'>
              Mensagem
          </button>
          <button className='ServiceLog-navButton'>
              Email
          </button>
          <button className='ServiceLog-navButton'>
              Mock
          </button>
        </nav>
        <div className='Service-payloads'>
          <div className='Service-payload'>
            <div className='Service-payloadCode'>
              <SyntaxHighlighter language={props.type} style={magula}>{props.request}</SyntaxHighlighter>
            </div>
          </div>
          <div className='Service-payload'>
            <div className='Service-payloadCode'>
              <SyntaxHighlighter language={props.type} style={magula}>{props.response}</SyntaxHighlighter>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}
