import React, { useState } from 'react'
import LogRequests from './LogRequests'
import LogInfo from './LogInfo'
import LogRequest from './LogRequest'

const exampleString = `{
  "active": true,
  "name": "Edward Norton",
  "createdAt": 1265068800000,
  "updatedAt": 1288322480000,
  "history": [{
    "id": "233421",
    "type": "product"
  }]
}`

export default function LogGroup (props) {
  const [record, setRecord] = useState(null)

  const handleClick = (clickedRecord) => {
    if (record && record.id === clickedRecord.id) {
      return setRecord(null)
    }
    setRecord({ ...clickedRecord })
  }

  return (
    <article className='ServiceLog-group'>
      <header className='ServiceLog-header'>
        <LogInfo infobox={props.group.infobox} />
        <LogRequests
          activeRecord={record}
          records={props.group.records}
          onClick={handleClick}
        />
      </header>
      {record && <LogRequest {...record} />}
    </article>
  )
}
