import React from 'react'

export default function LogRequests (props) {
  const activeRecord = (props.activeRecord || {})

  return (
    <div className='ServiceLog-requests'>
      {props.records.map((record) => (
        <button
          className={'ServiceLog-requestButton ' + (activeRecord.id === record.id && 'isActive')}
          key={record.id}
          onClick={props.onClick.bind(null, record)}
        >
          <p><em>{record.name}</em></p>
          <p>{record.requestTime.timeText}</p>
        </button>
      ))}
    </div>
  )
}
